.friendCard {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 170px;
  height: 170px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.friendCard > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}

.friendCard > .img-container > img {
  height: 100%;
}

.friendCard:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.friendCard:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
.wrapper {
  background: #FDC913;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  overflow: hidden;
}
.title {
    width: 100%;
    text-align: left;
    margin-left: 20px;
    font-size: 3rem;
    color: lightgoldenrodyellow;
    margin-top: 35px;
  }

.container-fluid {
    overflow: hidden;
    height: 100px;
}

.footer {
    background: goldenrod;

}
* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    
    background: #141414;
    padding: 0;
    margin: 0;
    height: 100%;
    max-height: 100%;
  }
